import React from 'react';
import Toast from '../Modals/Toast';
import SvgTrash from '../Svgs/SvgTrash';
import Flex from '../Utils/Flex/Flex';
import DatatableHeader from './DatatableHeader';
import Table from './Table';
import { ITable } from './table.types';

function DataTable({
  cols = [],
  rows = [],
  selection = false,
  limit = 10,
  skip = 0,
  total = 0,
  onNext,
  onPrevious,
  onSelectionChange,
  onSearch = (v) => v,
  onRowHover = (v) => v,
  selectedItems = [],
  onToastClosed = () => {},
  onRemoved = () => {},
}: ITable) {
  return (
    <div className="datatable">
      <DatatableHeader
        limit={limit}
        skip={skip}
        total={total}
        onNext={onNext}
        onPrevious={onPrevious}
        onSearch={onSearch}
      />
      <Table
        onSelectionChange={onSelectionChange}
        cols={cols}
        rows={rows}
        selection={selection}
        onRowHover={onRowHover}
      />
      <Toast
        ypos="bottom"
        duration={3600}
        open={selectedItems.length > 0}
        onClose={onToastClosed}
      >
        <Flex gap="40px">
          <div>
            {selectedItems.length} sélectionné
            {selectedItems.length === 1 ? '' : 's'}:
          </div>
          <Flex
            items="center"
            gap="10px"
            className="cursor-pointer"
            onClick={() => onRemoved(selectedItems)}
          >
            <SvgTrash />
            <div>Archiver</div>
          </Flex>
        </Flex>
      </Toast>
    </div>
  );
}

export default DataTable;
